<main>
  <h1>Chord Compose</h1>
  <Modal bind:open={modalOpen}>
    <Card>Score : {score}</Card>
  </Modal>
  <section>
    {#if inProgress}
      <p>
        { chord.join('') }
      </p>
      <p>
        <Icon icon="{faStopwatch}"/> {time}'
        | <Icon icon="{faStar}"/> {score}
      </p>
      <Button on:click="{generateChord}">
        <Icon icon="{faSyncAlt}"/>
        Swap
      </Button>
    {:else}
      <Button on:click="{start}">
        Go
      </Button>
    {/if}
  </section>
  <Keyboard on:pressed-change="{checkKeys}"
            bind:clear={clear} />
</main>

<script lang="ts">
import 'chota';
import { isEqual, uniq } from 'lodash';
import { Button, Modal, Card } from 'svelte-chota';
import Icon from 'fa-svelte';
import { faSyncAlt, faStopwatch, faStar } from '@fortawesome/free-solid-svg-icons';
import type ChordData from './types/ChordData';
import { SUCCESS_DELAY } from './consts';
import Note from './types/Note';
import type { NoteCollection } from './types/Note';
import Chord from './Chord';
import Keyboard from './components/Keyboard.svelte';

const GAME_DURATION = 30;

let chord: ChordData = [Note.C, ''];
let chordNotes: NoteCollection;
$: chordNotes = Chord.notes(chord);

let inProgress: boolean = false;
let score: number = 0;
let time: number = GAME_DURATION;
let intervalId: number;
let modalOpen: boolean = false;
let clear: boolean = false;


function generateChord(): void {
  chord = Chord.generate();
}

function start(): void {
  score = 0;
  generateChord();
  inProgress = true;
  intervalId = setInterval(() => time--, 1000);
}

function checkKeys({ detail: keys }: CustomEvent) {
  if (isEqual(uniq(keys.sort()), chordNotes.sort())) {
    clear = true;
    // TODO: score incr in timeout
    score++;
    setTimeout(generateChord, SUCCESS_DELAY);
  }
}

$: if (time === 0) {
  time = GAME_DURATION;
  inProgress = false;
  modalOpen = true;
  clearInterval(intervalId);
}
</script>

<style lang="scss">
main {
  text-align: center;
  padding: 1em;
  max-width: 240px;
  margin: 0 auto;

  p {
    font-size: 2rem;
  }

  section {
    min-height: 150px;
  }
}

h1 {
  color: #ff3e00;
  text-transform: uppercase;
  font-size: 4em;
  margin: 1rem 0;
}

@media (min-width: 640px) {
  main {
    max-width: none;
  }
}
</style>
