import { sample } from 'lodash';
import Note from "./types/Note";
const root = [
    Note.C, Note.D, Note.E, Note.F, Note.G, Note.A, Note.B,
];
const notes = Object.values(Note);
const chords = {
    '': [4, 3],
    'm': [3, 4],
    '7': [4, 3, 3],
    'sus2': [2, 5],
    'sus4': [5, 2],
    'add9': [4, 3, 7],
    'add11': [4, 3, 10],
    'add13': [4, 3, 2],
    'aug': [4, 4],
    'dim': [3, 3],
    '5': [7],
    'maj7': [4, 3, 4],
    'm7': [3, 4, 3],
    'dim7': [3, 3, 3],
    '9': [4, 3, 3, 4],
    '11': [4, 3, 3, 4, 3],
};
export default class Chord {
    static generate() {
        const chordRoot = sample(root);
        const chord = sample(Object.keys(chords));
        return [chordRoot, chord];
    }
    static notes([root, chordType]) {
        const chordsNotes = [root];
        const interval = chords[chordType];
        let idx = notes.indexOf(root);
        for (let i = 0, l = interval.length; i < l; ++i) {
            idx = (idx + interval[i]) % notes.length;
            chordsNotes.push(notes[idx]);
        }
        return chordsNotes;
    }
}
