<main class="keyboard">
  <section class="keyboard__keys">
    {#each keys as { note, style, isPressed }, i}
      <Key note="{note}"
           index="{Math.floor(i / 12)}"
           sampler="{sampler}"
           style="{style}"
           bind:pressed={isPressed}
           on:key-press={handleKeyPress} />
    {/each}
  </section>

  <section class="keyboard__pressed-notes">
    {#each pressedNotes as note}
      <Tag class="{noteTagClass}">
        { note }
      </Tag>
    {/each}
  </section>
  <Button on:click="{clearKeyboard}">
    <Icon icon="{faTrashAlt}"/>
    Clear
  </Button>
</main>

<script lang="ts">
import * as Tone from 'tone';
import { Button, Tag } from 'svelte-chota';
import Icon from 'fa-svelte';
import { faTrashAlt } from '@fortawesome/free-solid-svg-icons/faTrashAlt';
import { createEventDispatcher, onMount } from 'svelte';
import { SUCCESS_DELAY } from '../consts';
import Key from './Key.svelte';
import type Note from '../types/Note';
import type { NoteCollection } from '../types/Note';
import type iKey from '../types/Key';

export let clear: boolean;

let success: boolean = false;
let pressed: iKey[] = [];
let noteTagClass: string;
let sampler: Tone.Sampler;
let keys: { note: Note, style?: string, isPressed: boolean }[] = [
  { note: "C", isPressed: false },
  { note: "C#", style: shift(0), isPressed: false },
  { note: "D", isPressed: false },
  { note: "D#", style: shift(1), isPressed: false },
  { note: "E", isPressed: false },
  { note: "F", isPressed: false },
  { note: "F#", style: shift(3), isPressed: false },
  { note: "G", isPressed: false },
  { note: "G#", style: shift(4), isPressed: false },
  { note: "A", isPressed: false },
  { note: "A#", style: shift(5), isPressed: false },
  { note: "B", isPressed: false },
  { note: "C", isPressed: false },
  { note: "C#", style: shift(7), isPressed: false },
  { note: "D", isPressed: false },
  { note: "D#", style: shift(8), isPressed: false },
  { note: "E", isPressed: false },
  { note: "F", isPressed: false },
  { note: "F#", style: shift(10), isPressed: false },
  { note: "G", isPressed: false },
  { note: "G#", style: shift(11), isPressed: false },
  { note: "A", isPressed: false },
  { note: "A#", style: shift(12), isPressed: false },
  { note: "B", isPressed: false },
];

const dispatch = createEventDispatcher();
onMount(() => {
  sampler = buildSampler();
});

function buildSampler(): Tone.Sampler {
  return new Tone.Sampler({
    urls: {
      C4: "C4.mp3",
      "D#4": "Ds4.mp3",
      "F#4": "Fs4.mp3",
      A4: "A4.mp3",
      C5: "C5.mp3",
      "D#5": "Ds5.mp3",
      "F#5": "Fs5.mp3",
      A5: "A5.mp3",
    },
    release: 1,
    baseUrl: "https://tonejs.github.io/audio/salamander/"
  }).toDestination();
}

function shift(threshold: number): string {
  return `left: ${threshold*7+5}%`;
}

function handleKeyPress({ detail: keyData }: CustomEvent) {
  const keyIndex = pressed.findIndex(k => {
      return compareKey(k, keyData)
  });
  if (keyIndex >= 0) {
    const tempArr = [...pressed];
    tempArr.splice(keyIndex, 1);
    pressed = tempArr;
    return;
  }
  pressed = [...pressed, keyData];
}

function compareKey(keyA: iKey, keyB: iKey): boolean {
  return keyA.note === keyB.note
          && keyA.index === keyB.index;
}

async function clearKeyboard({ delay } = { delay: false }) {
  if (delay)
    await new Promise(res => setTimeout(res, SUCCESS_DELAY))
  keys = keys.map(key => ({ ...key, isPressed: false }));
  pressed = [];
}

$: if (clear) {
  clearKeyboard({ delay: true })
  clear = false;
  success = true;
  setTimeout(() => success = false, SUCCESS_DELAY);
}
$: dispatch('pressed-change', pressedNotes);

let pressedNotes: NoteCollection;
$: pressedNotes = pressed.map(key => key.note);
$: {
  const classes = 'text-white bd-light';
  noteTagClass =  classes + (success ? ' bg-success' : ' bg-grey');
}
</script>

<style lang="scss">
.keyboard {
  &__keys {
    display: block;
    height: 350px;
    position: relative;
    max-width: 1500px;
    margin: auto;
    margin-top: 40px;
  }
  &__pressed-notes {
    margin-top: 1rem;
    font-size: 1.8em;
    min-height: 60px;
  }
}
</style>
