<div class="key"
     class:sharp="{isSharp()}"
     class:pressed
     style="{style}"
     on:click="{press}">
</div>

<script lang="ts">
import { createEventDispatcher } from 'svelte';
import type { Sampler } from 'tone';
import type Note from '../types/Note';

export let note: Note;
export let index: number;
export let style: string = '';
export let pressed: boolean;
export let sampler: Sampler;

const dispatch = createEventDispatcher();

function press() {
  pressed = !pressed;
  if (pressed)
    playSound();
  dispatch('key-press', { note, index });
}

function playSound(): void {
  sampler.triggerAttackRelease(
    `${note}${4 + index}`, 1
  );
}

function isSharp(): boolean {
  return note.includes('#');
}
</script>

<style lang="scss">
.key {
  position: relative;
  border: 4px solid black;
  border-radius: .5rem;
  transition: all .07s ease;
  display: block;
  box-sizing: border-box;
  z-index: 2;
  cursor: pointer;

  &:not(.sharp) {
    float: left;
    width: 7%;
    height: 100%;
    background: rgba(255, 255, 255, .8);

    &.pressed {
      background: rgba(lightgrey, .8);
    }
  }

  &.sharp {
    position: absolute;
    width: 4%;
    height: 60%;
    background: #000;
    color: #eee;
    top: 0;
    z-index: 3;
    &.pressed {
      background: #666;
    }
  }

  &.pressed {
    transform: scale(.95);
  }
}
</style>
